import React, { useState, useEffect } from 'react';
import {
  Container,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogActions,
  Button,
  IconButton,
  TextField,
  MenuItem,
  FormControl,
  InputLabel,
  Select,
} from '@mui/material';
import WifiIcon from '@mui/icons-material/Wifi';
import InputAdornment from '@mui/material/InputAdornment';
import VisibilityIcon from '@mui/icons-material/Visibility';
import VisibilityOffIcon from '@mui/icons-material/VisibilityOff';


const WifiList = () => {
  const [wifiList, setWifiList] = useState([]);
  const [selectedNetwork, setSelectedNetwork] = useState('');
  const [password, setPassword] = useState('');
  const [showPassword, setShowPassword] = useState(false); // New state for password visibility
  const [isDialogOpen, setDialogOpen] = useState(false);
  const [connectedNetwork, setConnectedNetwork] = useState('');


  useEffect(() => {
    fetch('/service/get-wifi-lists')
      .then(response => response.json())
      .then(data => {
        if (Array.isArray(data.ssids)) {
          setWifiList(data.ssids);
        } else {
          console.error('Invalid data format:', data);
        }
      })
      .catch(error => console.error('Error fetching data:', error));

    // Fetch connected network info
    fetch('/service/get-connected-network')
      .then(response => response.json())
      .then(data => {
        if (data.connectedNetwork) {
          setConnectedNetwork(data.connectedNetwork);
        }
        console.log('data: ', data.connectedNetwork);
      })
      .catch(error => console.error('Error fetching connected network data:', error));
    }, []);

  const handleDialogOpen = () => {
    setDialogOpen(true);
  };

  const handleDialogClose = () => {
    setSelectedNetwork('');
    setPassword('');
    setDialogOpen(false);
  };

  const handleNetworkChange = (event) => {
    setSelectedNetwork(event.target.value);
  };

  const handlePasswordChange = (event) => {
    setPassword(event.target.value);
  };

  const handleTogglePasswordVisibility = () => {
    setShowPassword(!showPassword);
  };

  const handleSubmit = () => {
    // Check if both ssid and password are provided before making the request
    if (!selectedNetwork || !password) {
      console.error('SSID and password are required.');
      return;
    }
  
    // Create JSON object
    const jsonData = {
      ssid: selectedNetwork,
      password: password,
    };
  
    // Make a POST request to your Flask API endpoint
    fetch('/service/connect-network', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/json',
      },
      body: JSON.stringify(jsonData),
    })
      .then(response => response.text())
      .then(data => {
        console.log('Server response:', data);
        // Add any additional logic based on the server response
      })
      .catch(error => {
        console.error('Error connecting to the network:', error);
      });
  
    handleDialogClose(); // Close the dialog after submission
  };
  

  return (
    <Container>
      <div style={{ position: 'absolute', top: 0, right: 0, padding: '10px', marginRight: '225px'}}>
        <IconButton onClick={handleDialogOpen} color="primary">
          <WifiIcon />
        </IconButton>
      </div>

      <Dialog open={isDialogOpen} onClose={handleDialogClose}>
        <DialogTitle>Wi-Fi</DialogTitle>
        <DialogContent>
          <div style={{ display: 'flex', flexDirection: 'column', alignItems: 'center' }}>
            <FormControl>
              <InputLabel id="network-select-label" style={{ marginTop: '10px' }}>Available-Network</InputLabel>
              <Select
                labelId="network-select-label"
                id="network-select"
                value={selectedNetwork}
                onChange={handleNetworkChange}
                label="Available-Network"
                style={{ marginTop: '10px', width: '200px' }}
              >
                {wifiList.map((ssid, index) => (
                  <MenuItem key={index} value={ssid}>
                    {ssid} {connectedNetwork === ssid && '✔️'}
                  </MenuItem>
                ))}
              </Select>
            </FormControl>
            <TextField
              margin="normal"
              label="Password"
              type={showPassword ? 'text' : 'password'}
              value={password}
              onChange={handlePasswordChange}
              style={{ marginTop: '10px', width: '200px' }}
              InputProps={{
                endAdornment: (
                  <InputAdornment position="end">
                    <IconButton onClick={handleTogglePasswordVisibility}>
                      {showPassword ? <VisibilityOffIcon fontSize="small"/> : <VisibilityIcon fontSize="small"/>}
                    </IconButton>
                  </InputAdornment>
                ),
              }}
            />
          </div>
        </DialogContent>
        <DialogActions>
          <Button onClick={handleDialogClose} color="primary">
            Cancel
          </Button>
          <Button onClick={handleSubmit} color="primary">
            Connect
          </Button>
        </DialogActions>
      </Dialog>
    </Container>
  );
};

export default WifiList;

